<template>
  <div class="container">
    <input type="hidden" v-model="no"/>
    <div>
        <label for="question" class="form-label"></label>
        <input type="text" v-model="question" class="form-control" placeholder=" "/>
    </div>
    <div>
        <label for="content" class="form-label"></label>
        <textarea type="text" v-model="content" class="form-control" placeholder=" "/>
    </div>
    <div class="d-grid gap-2 d-md-flex justify-content-md-end mt-3">    
      <button @click="faqupdate" class="btn new-btn"></button>
      <button @click="$router.go(-1)" class="btn btn-secondary"></button>
    </div>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  name: 'FaqUpdate',
  data() {
    return {
      faq: {},
      no: this.$route.params.num, // $route.params.num  
      question: '',
      content: '',
    }
  },
  methods: {
    faqupdate: function () {
      axios.put('/project/api/faq/' + this.no + '/update',
        { faqNum: this.no, question: this.question, content: this.content }
      ).then(response => {
        console.warn(response)
        this.question = '';
        this.content = '';
        this.result = response.data;
        this.$router.push('/faq'); //    faq  
      }).catch((ex) => {
        console.warn("ERROR!!!!! : ", ex)
      })
    },
  }
};
</script>